@import url(https://fonts.googleapis.com/css2?family=Lato&family=Nunito:wght@300;400;600;700&display=swap);
.auto-complete-input {
  padding: 0;
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
}

.auto-complete-input__input-box {
  border: none;
  width: 100%;
}

.auto-complete-input__form {
  flex-grow: 1;
  margin-right: 10px;
}

.auto-complete-input:active {
  border-color: var(--g3-color__base-blue);
} 

.auto-complete-input__close {
  width: 14px;
  height: 14px;
  position: relative;
  top: 4px;
  background-color: var(--g3-color__gray);
  cursor: pointer;
}

.auto-complete-input__separator {
  width: 1px;
  border-left: 1px solid var(--g3-color__lightgray);
  margin: 0 10px;
}

.auto-complete-input__icon {
  background-color: var(--g3-color__gray);
  position: relative;
  top: 3px;
  cursor: pointer;
}

:root {
  --g3-auto-complete__height: 45px;
  --g3-auto-complete__padding: 10px;
  --g3-auto-complete__suggestion-color: var(--g3-color__black);
  --g3-auto-complete__suggestion-color--highlight: var(--g3-color__base-blue);
  --g3-auto-complete__suggestion-color--hover: var(--g3-color__white);
  --g3-auto-complete__suggestion-color--hover-higlight: var(--g3-color__highlight-orange);
  --g3-auto-complete__suggestion-bg-color: var(--g3-color__white);
  --g3-auto-complete__suggestion-bg-color--hover: var(--g3-color__base-blue);
  --g3-auto-complete__border-radius: 4px;
}
  
.auto-complete {
  border: 1px solid var(--g3-color__lightgray);
  border-radius: 4px;
  border-radius: var(--g3-auto-complete__border-radius);
}
  
.auto-complete__input-wrapper {
  height: 45px;
  height: var(--g3-auto-complete__height);
  box-sizing: border-box; /* include padding from height*/
  width: calc(100% - 2px); /* for borders and paddings */
  padding: 10px;
  padding: var(--g3-auto-complete__padding);
}
:root {
  --dictionary-header-color: var(--g3-color__gray);
  --dictionary-border-color: var(--g3-color__silver);
  --dictionary-border-color--hover: var(--g3-color__lightgray);
}


.data-dictionary-property-table__table {
  background-color: var(--g3-color__white);
  border-collapse: collapse;
  width: 100%;
}

.data-dictionary-property-table__data {
  padding: 10px 10px 10px 19px;
  border: 0;
  text-align: left;
  font-family: raleway;
  vertical-align: top;
}

td.data-dictionary-property-table__data > p {
  margin: auto;
}

td.data-dictionary-property-table__data:nth-child(2) > p {
  max-width: 300px;
  min-width: 100px;
  word-wrap: break-word;
}

.data-dictionary-property-table--without-border tr:first-child th.data-dictionary-property-table__data {
  border-top: 0;
}
.data-dictionary-property-table--without-border tr:last-child td.data-dictionary-property-table__data {
  border-bottom: 0;
}
.data-dictionary-property-table--without-border tr td.data-dictionary-property-table__data:first-child,
.data-dictionary-property-table--without-border tr th.data-dictionary-property-table__data:first-child {
  border-left: 0;
}

.data-dictionary-property-table--without-border tr td.data-dictionary-property-table__data:last-child,
.data-dictionary-property-table--without-border tr th.data-dictionary-property-table__data:last-child {
  border-right: 0;
}

.data-dictionary-property-table__head {
  color: #606060;
  font-size: 13px;
  background: #fff;
  border-top: 2px solid #606060;
  border-bottom: 2px solid #606060;
}

.data-dictionary-property-table__head th {
  font-weight: 900;
}

.data-dictionary-property-table__row:nth-child(odd) {
  background: #f4f5f5;
}

.data-dictionary-property-table__row:nth-child(even) {
  background: #fff;
}

.data-dictionary-property-table__required {
  color: #ff5a20;
  font-size: 13px;
  font-weight: 900;
}

.data-dictionary-property-table__required-icon {
  background-color: var(--g3-color__highlight-orange);
}

.data-dictionary-property-table__span {
  display: inline;
  font-family: raleway;
}

.data-dictionary-property-table__span--highlight {
  /* color: var(--g3-color__highlight-orange); */
  color: var(--g3-color__black);
  font-size: 14px;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 900;
}

.data-dictionary-property-table__span--new-line {
  display: block;
}

.data-dictionary-property-table__data_KeyProperty {
  /* float: left; */
  display: inline-block;
  min-width: 220px;
  font-weight: bold;
  color: #0d71a3;
}

.data-dictionary-property-table__data_KeyProperty p {
  float: left;
  margin: auto;
}

.data-dictionary-property-table__data_KeyProperty_icon {
  width: 25px;
  margin-left: 8px;
  padding-top: 5px;
}

.data-dictionary-property-table_key_description > span:last-child:not(:first-child) {
  display: block;
  margin-top: 13px;
}

ul.data-dictionary-property-table__span{
  list-style-type: none;
}

.data-dictionary-node {
  background-color: #f4f5f5;
  border-right: 1px solid var(--dictionary-border-color);
  display: flex;
  justify-content: space-between;
  padding: 15px 5px 14px 22px;
  border-left-width: 5px;
  border-left-style: solid;
  border-left-color: var(--dictionary-border-color);
  cursor: pointer;
  margin-bottom: 5px;
}

.data-dictionary-node:last-child {
  margin-bottom: 0;
}

.data-dictionary-node__title {
  width: 260px;
  flex-grow: 0;
  flex-shrink: 0;
  -moz-user-select: none;
  -webkit-user-select: none;
  font-weight: 900;
  user-select: none;
  font-size: 15px;
  line-height: 14px;
}

.data-dictionary-node:hover .data-dictionary-node__title {
  color: var(--g3-color__base-blue);
}

.data-dictionary-node__file-icon {
  margin-right: 10px;
  position: relative;
  top: 3px;
}

.data-dictionary-node__toggle-icon {
  margin-left: 10px;
  position: relative;
  top: 3px;
}

.data-dictionary-node__description {
  flex-grow: 3;
  padding-right: 21px;
  font-size: 14px;
  line-height: 17px;
  max-height: 65px;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-top: 10px;
  padding-left: 0;
}

.data-dictionary-node__label {
  margin-top: 10px;
  float: left;
  color: #8e8e8e;
  font-weight: 900;
  margin-right: 5px;
  border-radius: 100px;
  border: 1px solid #cdcdcd;
  text-align: center;
  padding: 2px 12px;
  background: #fff;
  font-size: 12px;
}

.data-dictionary-node__assignment_group {
  text-align: right;
}

.data-dictionary-node__class,
.data-dictionary-node__assignment {
  margin-left: 5px;
  color: #2982af;
  font-weight: 500;
}

.data-dictionary-node__class_group {
  padding-left: 18px;
}

.data-dictionary-node__download-button-group {
  text-align: right;
  margin-top: -5px;
  padding-right: 15px;
}

.data-dictionary-node__download-group {
  display: inline;
  flex-grow: 0;
  flex-shrink: 0;
  padding-left: 5px;
}

.data-dictionary-node__button-wrap {
  float: right;
  margin-top: -8px;
}

.data-dictionary-node__button-wrap:last-child {
  border-right: none;
}

.data-dictionary-node__download-button_tsv {
  padding: 0;
  width: 35px;
  border: none;
  background-color: inherit;
  margin-right: 10px;
}

.data-dictionary-node__download-button-group_loading {
  opacity: 0.5;
  color: #f4f5f5;
  background-color: #0a4a6d;
  float: left;
  width: 400px;
}

.data-dictionary-node__download-button_pdf {
  height: 30px;
  margin: 5px 6px 0 6px;
  padding: unset; /* override .g3-button's paddings */
  font-weight: normal;
  display: block;
  color: var(--g3-color__white);
  text-align: right;
  border: none;
}

.data-dictionary-node__loading {
  padding: 5px 5px;
  background-color: #0b3557;
  color: #fff;
}

.data-dictionary-node__download-button_pdf > img {
  width: 35px;
  margin-top: -5px;
}

.data-dictionary-node__download-button_tsv:hover,
.data-dictionary-node__download-button:active {
  cursor: pointer;
}

.data-dictionary-node__download-button .g3-icon {
  position: relative;
  top: 1px;
  left: -2px;
}

.data-dictionary-node__multiple-download-button {
  width: 180px;
  min-width: unset; /* override .g3-button's 152px min-width */
  height: 30px;
  margin: 0 6px;
  padding: unset; /* override .g3-button's paddings */
  font-weight: normal;
  display: inline;
  background-color: #0b3557;
  color: var(--g3-color__white);
  padding: 5px 6px 5px 6px;
  border-top: 1px solid #cccccc;
  border-right: 1px solid #333333;
  border-bottom: 1px solid #333333;
  border-left: 1px solid #cccccc;
}

.data-dictionary-node__multiple-download-button:hover,
.data-dictionary-node__multiple-download-button:active {
  color: white;
  background-color: var(--g3-color__base-blue);
}

.data-dictionary-node__multiple-download-button .g3-icon {
  position: relative;
  top: 1px;
  left: -2px;
}

.data-dictionary-node__property {
  background-color: #fff;
  padding: 10px 18px 18px 23px;
  position: relative;
  margin: 0;
}

.data-dictionary-node__property-summary {
  /* margin: 5px 0; */
  color: #7a7a7a;
  padding-left: 17px;
  margin-bottom: 8px;
}

.data-dictionary-node__property-close {
  float: right;
  cursor: pointer;
}

.data-dictionary-node__property-close:hover {
  color: var(--g3-color__black);
}

.data-dictionary-node__property-close-icon {
  position: relative;
  top: 4px;
  margin-right: 20px;
  background-color: var(--g3-color__gray);
}

.data-dictionary-node__property-close:hover .data-dictionary-node__property-close-icon {
  background-color: var(--g3-color__black);
}

